<template>
  <div>
    <van-nav-bar
      :title="title"
      left-text="返回"
      left-arrow
      @click-left="onClickLeft"
    />

    <van-cell-group>
      <van-cell :title="item.title" is-link :to="item.path" v-for="item in list"/>
    </van-cell-group>

  </div>
</template>

<script>
  export default {
    name: "detail",
    data(){
      return {
        title:'',
        titles: ['中诚信', '中债资'],
        list: [
          {title: '业务介绍', path: ''},
          {title: '评级报告', path: ''},
          {title: '研究报告', path: ''},
          {title: '付费报告', path: ''},
          {title: '沟通交流', path: ''},
        ]
      }
    },
    methods:{
      onClickLeft(){
        this.$router.back();
      }
    },
    created() {
      this.title = this.titles[this.$route.params.id];
    }
  }
</script>

<style scoped>
</style>



